.tree {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 300px;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: rgb(238, 241, 245);
}

.m-node.placeholder {
  border: 1px dashed #1385e5;
}

.m-node .inner {
  
  font-size: 18px;

  display: -ms-flexbox;

  display: flex;
  padding:5px 2px 5px 15px ;
}
.m-node .inner:hover{
  background-color: rgba(0,0,0,0.1)
}

.m-node .node {
  display: inline-block;
  width: 100%;
  padding: 25px;
}

.m-node .node.is-active {
  background-color:rgb(250, 250, 250)
}
.nodeD{
  display:inline-block;
  font-size: 12px;
  padding-top: 15px;
  padding-left: 10px
 
}
.datafld{
  background-color:transparent;
  border: none;

  padding: 10px;

  font-size: 14px;
  width:150px;
 
}


